import XEUtils from 'xe-utils';
import fulfillMentStatusTag from './../../components/ShopifyStatus';
import DistributionVendorDialog from "@/components/DistributionVendorDialog";
import popShippingJson from './../../components/PopShippingJson';
import shopCate from './../../components/shopCate';
export default {
  data: function data() {
    return {
      AllChecked: false,
      options: [{
        id: 1,
        label: 'Shopify',
        children: []
      }, {
        id: 2,
        label: 'Woocommerce',
        children: []
      }, {
        id: 3,
        label: 'Lazada',
        children: []
      }, {
        id: 4,
        label: 'Shopee',
        children: []
      }],
      cascaderKey: 1,
      loading: false,
      tableHeight: 400,
      isAllExpand: true,
      tableData: [],
      tableDataCopy: [],
      pageSize: [10, 20, 50],
      page: this.$route.query.page ? Number(this.$route.query.page) : 1,
      rowsPerPage: localStorage.getItem("c_waitingForAllocatedRowsPerPage") ? Number(localStorage.getItem("c_waitingForAllocatedRowsPerPage")) : 10,
      totalCount: 0,
      totalPage: 0,
      allExpand: false,
      //全部展开
      ce: this.$CE,
      //货币符号
      isManage: 1,
      codeName: "",
      filterParams: {
        codeName: '',
        shopifyStore: [],
        fulfillStatus: [],
        timeFilter: "",
        dateFrom: "",
        dateTo: "",
        sortBy: ""
      },
      filterParamsDefault: "{}",
      storeList: [],
      shopifyStoreName: "",
      popStoreFilter: false,
      popFulfillFilter: false,
      popDateFilter: false,
      popSortFilter: false,
      filterDate: [],
      dateArr: {
        1: 'Today',
        2: 'Last 7 days',
        3: 'Last 30 days',
        4: 'Last 90 days',
        5: 'Last 12 months',
        6: 'Custom'
      },
      sortArr: {
        1: 'Order number (ascending)',
        2: 'Order number (descending)',
        3: 'Date (oldest first)',
        4: 'Date (newest first)'
      },
      dialogAllocated: {
        status: 1,
        isShow: false,
        loading: false,
        skus: [],
        relationshipId: "",
        vendors: [],
        vendorLike: "",
        page: 1,
        rowsPerPage: 5,
        total: 0,
        totalPage: 0,
        vendor: null,
        other: [],
        allChecked: false
      },
      dialogAllocatedDefault: '{}',
      ball: {
        x: window.innerWidth / 2,
        y: window.innerHeight / 2,
        opacity: 0
      },
      bottomLoading: false,
      scrollT: 0,
      gvTime: 0,
      keyTimeout: 0
    };
  },
  components: {
    fulfillMentStatusTag: fulfillMentStatusTag,
    DistributionVendorDialog: DistributionVendorDialog,
    popShippingJson: popShippingJson,
    shopCate: shopCate
  },
  watch: {
    $route: "gotoPage",
    // tableHeight(){
    //   this.$nextTick(()=>{
    //     this.refreshTree();
    //   })
    // },
    CAllChecked: function CAllChecked() {
      this.AllChecked = this.CAllChecked.status;
    },
    'filterParams.shopifyStore': function filterParamsShopifyStore() {
      var _this = this;

      if (!this.filterParams.shopifyStore.length) return;
      var arr = [];
      this.filterParams.shopifyStore.forEach(function (e) {
        _this.storeList.forEach(function (item) {
          if (e == item.shopifyAccountId) {
            arr.push(item.store);
          }
        });
      });
      this.shopifyStoreName = arr.join(",");
    },
    isShowFilter: function isShowFilter() {
      if (this.isShowFilter) {// this.filterParams.timeFilter = 3;
      }
    },
    allocatedOtherSelectNum: function allocatedOtherSelectNum() {
      if (!this.dialogAllocated.other.length) {
        this.$set(this.dialogAllocated, 'allChecked', false);
        return;
      }

      this.$set(this.dialogAllocated, 'allChecked', this.allocatedOtherSelectNum == this.dialogAllocated.other.length);
    }
  },
  mounted: function mounted() {
    var _this2 = this;

    this.filterParamsDefault = JSON.stringify(this.filterParams);
    this.dialogAllocatedDefault = JSON.stringify(this.dialogAllocated); // this.filterParams.timeFilter = 3;

    if (this.$route.params.vid == '999e') {
      this.filterParams.timeFilter = "";
    }

    if (this.$route.params.id) {
      this.filterParams.timeFilter = "";
    }

    this.$nextTick(function () {
      // this.$emit("getTableHeight", this);
      _this2.getStore(); // window.onresize = () => {
      //   this.$emit("getTableHeight", this);
      // };

    }); //打一次接口再打开子路由

    this.loading = true;
    this.$apiCall('api.ShopifyAccount.updateShopifyOrderNow', {}, function (r) {
      _this2.getItems();
    });
  },
  beforeDestroy: function beforeDestroy() {// window.onresize = null;
  },
  computed: {
    isShowFilter: function isShowFilter() {
      return !this.filterParams.sortBy && !this.filterParams.fulfillStatus.length && !this.filterParams.shopifyStore.length && !this.filterParams.codeName;
    },
    allocatedOtherSelectNum: function allocatedOtherSelectNum() {
      var num = 0;
      this.dialogAllocated.other.forEach(function (e) {
        if (e.checked) {
          num++;
        }
      });
      return num;
    },
    checkedOrder: function checkedOrder() {
      //选中的id 数组
      return this.tableDataCopy.filter(function (order) {
        return order.checked;
      });
    },
    CAllChecked: function CAllChecked() {
      //是否全选
      var num = 0,
          skus = [];
      this.tableDataCopy.forEach(function (order) {
        if (order.checked) {
          num++;

          if (order.items) {
            order.items.forEach(function (itm) {
              skus.push(itm.id);
            });
          } else {
            skus.push(order.itemId);
          }
        }
      });
      return {
        num: num,
        status: num && num == this.tableDataCopy.length,
        skus: skus
      };
    }
  },
  methods: {
    makeAnOffer: function makeAnOffer() {
      var _this3 = this;

      //生成 make an offer
      if (!this.CAllChecked.num) {
        this.$message({
          message: 'Please select at least one order!',
          type: 'error'
        });
        return false;
      }

      this.$Burying({
        object: '14001'
      });
      this.$confirm("Create quotation request?", "", {
        confirmButtonText: "OK",
        cancelButtonText: "Discard",
        type: "info"
      }).then(function () {
        _this3.loading = true;

        _this3.$apiCall("api.OfferList.genOffer", {
          skus: _this3.CAllChecked.skus
        }, function (r) {
          _this3.loading = false;

          if (r.ErrorCode == "9999") {
            _this3.$elementMessage("success", "success");

            _this3.$router.push({
              name: "WaitingForVendorsRespond"
            });
          } else {
            _this3.$elementMessage(r.Message, "error");
          }
        });
      });
      setTimeout(function () {
        //取消弹层显示时 按钮默认 focus
        document.querySelectorAll(".el-message-box__btns .el-button").forEach(function (e) {
          e.blur();
        });
      }, 200);
    },
    toPage: function toPage(val) {
      if (val != this.$route.query.page) this.$router.push({
        query: {
          page: val
        }
      });
    },
    changePageSize: function changePageSize(val) {
      this.rowsPerPage = val;
      localStorage.setItem("c_waitingForAllocatedRowsPerPage", val);
      this.getItems();
    },
    gotoPage: function gotoPage() {
      this.page = this.$route.query.page ? this.$route.query.page : 1;
      this.getItems();
    },
    listScroll: function listScroll() {
      var _this4 = this;

      this.scrollT = setTimeout(function () {
        clearTimeout(_this4.scrollT);
        var scrollT = document.querySelector(".vxe-list--virtual-wrapper").scrollTop;
        var h = document.querySelector(".vxe-list--body").offsetHeight;
        var mT = document.querySelector(".vxe-list--body").style.marginTop ? document.querySelector(".vxe-list--body").style.marginTop.match(/\d+/g)[0] : 0;
        h = h + Number(mT);

        if (scrollT + _this4.tableHeight + 100 > h) {
          if (_this4.page <= _this4.totalPage) {
            _this4.getItems(2);
          }
        }
      }, 200);
    },
    ballAnimation: function ballAnimation() {
      var _this5 = this;

      //重新获取数量
      this.$emit('getNum', new Date().valueOf());

      var getOffset = function getOffset(element) {
        var actualTop = element.offsetTop;
        var actualLeft = element.offsetLeft;
        var current = element.offsetParent;

        while (current !== null) {
          actualTop += current.offsetTop;
          actualLeft += current.offsetLeft;
          current = current.offsetParent;
        }

        return {
          left: actualLeft,
          top: actualTop
        };
      };

      var elOffset = getOffset(document.getElementById('tab-WaitingForSourcing'));
      this.ball.opacity = 1;
      this.ball.x = elOffset.left + 160;
      this.ball.y = elOffset.top + 20;
      setTimeout(function () {
        _this5.ball.opacity = 0;
      }, 500);
      setTimeout(function () {
        _this5.ball.x = window.innerWidth / 2, _this5.ball.y = window.innerHeight / 2;
      }, 1000);
    },
    closeAllocated: function closeAllocated() {
      //Allocated关闭后处理
      if (this.dialogAllocated.status == 2) {
        this.ballAnimation();
      }
    },
    changeAllChecked: function changeAllChecked() {
      var _this6 = this;

      this.dialogAllocated.other.forEach(function (e) {
        _this6.$set(e, 'checked', _this6.dialogAllocated.allChecked);
      });
    },
    changeAllCheckedT: function changeAllCheckedT(bool) {
      var _this7 = this;

      this.AllChecked = bool; //全选全不选

      this.tableDataCopy.forEach(function (e) {
        _this7.$set(e, "checked", _this7.AllChecked);
      });
    },
    AllocateNow: function AllocateNow() {
      var _this8 = this;

      this.dialogAllocated.loading = true;
      var skus = this.dialogAllocated.other.map(function (sku) {
        if (sku.checked) {
          return sku.variableId;
        }
      });
      var params = {
        relationshipId: this.dialogAllocated.vendor.id,
        skus: skus,
        notProduct: 1
      };
      this.$apiCall('api.DispatchOrderRule.dispatchProducts', params, function (r) {
        _this8.dialogAllocated.loading = false;

        if (r.ErrorCode == "9999") {
          _this8.dialogAllocated.isShow = false;

          _this8.$elementMessage('Allocated Success', "success");

          _this8.resetPage();
        } else {
          _this8.$elementMessage(r.Message, "error");
        }
      });
    },
    selectVendor: function selectVendor(item) {
      var _this9 = this;

      this.dialogAllocated.loading = true;
      var skus = this.dialogAllocated.skus.map(function (sku) {
        return sku.variableId;
      });
      var params = {
        relationshipId: item.id,
        skus: skus
      };
      this.$apiCall('api.DispatchOrderRule.dispatchProducts', params, function (r) {
        _this9.dialogAllocated.loading = false;

        if (r.ErrorCode == "9999") {
          _this9.dialogAllocated.vendor = item;
          _this9.dialogAllocated.allChecked = true;
          _this9.dialogAllocated.other = r.Data.Results.map(function (e) {
            e.checked = true;
            return e;
          });
          _this9.dialogAllocated.status = 2;

          _this9.resetPage();
        } else {
          _this9.$elementMessage(r.Message, "error");
        }
      });
    },
    getVendors: function getVendors(query) {
      var _this10 = this;

      clearTimeout(this.gvTime);
      this.gvTime = setTimeout(function () {
        if (query) {
          _this10.dialogAllocated.page = query;
        }

        if (!_this10.dialogAllocated.vendorLike) _this10.dialogAllocated.loading = true;

        _this10.$apiCall("api.Relationship.findByCustomer", {
          page: _this10.dialogAllocated.page,
          rowsPerPage: _this10.dialogAllocated.rowsPerPage,
          vendorLike: _this10.dialogAllocated.vendorLike
        }, function (r) {
          if (r.ErrorCode == "9999") {
            _this10.dialogAllocated.loading = false;
            _this10.dialogAllocated.vendors = r.Data.Results;
            _this10.dialogAllocated.total = Number(r.Data.Pagination.totalCount);
          } else {
            _this10.$elementMessage(r.Message, "error");
          }
        });
      }, 200);
    },
    openAllocateToVendor: function openAllocateToVendor(item) {
      //打开分配弹层
      // this.dialogAllocated = JSON.parse(this.dialogAllocatedDefault);
      if (item._LEVEL === 0) {
        // this.dialogAllocated.skus = item.items.slice()
        this.$refs.DistributionVendorDialog.openDistributionDialog(item.items.slice());
      } else {
        // this.dialogAllocated.skus = [item]
        this.$refs.DistributionVendorDialog.openDistributionDialog([item]);
      } // this.getVendors();
      // this.dialogAllocated.isShow = true;

    },
    changeAllExpand: function changeAllExpand() {
      this.isAllExpand = !this.isAllExpand;
      this.allTreeExpand();
    },
    allTreeExpand: function allTreeExpand() {
      if (this.isAllExpand) {
        XEUtils.eachTree(this.tree6, function (item) {
          item._EXPAND = item._HAS_CHILDREN;
          item._VISIBLE = true;
        });
      } else {
        XEUtils.eachTree(this.tree6, function (item) {
          item._EXPAND = false;
          item._VISIBLE = !item._LEVEL;
        });
      }

      this.refreshTree();
    },
    resetPage: function resetPage() {
      if (this.$route.query.page == 1) {
        this.getItems();
      } else {
        this.$router.push({
          query: {
            page: 1
          }
        });
      }
    },
    dateChange: function dateChange() {
      if (this.filterDate.length > 1) {
        this.filterParams.dateFrom = moment(this.filterDate[0]).unix();
        this.filterParams.dateTo = moment(this.filterDate[1]).endOf("d").unix(); //筛选

        this.resetPage();
      }
    },
    filterItems: function filterItems(type) {
      switch (type) {
        case 'name':
          this.filterParams.codeName = this.codeName;
          break;

        case 'date':
          this.filterDate = [];
          this.filterParams.dateFrom = "";
          this.filterParams.dateTo = "";
          break;

        default:
          break;
      }

      if (this.timeFilter != 6) this.resetPage();
    },
    clearFilter: function clearFilter(type) {
      switch (type) {
        case 'name':
          this.filterParams.codeName = this.codeName = '';
          break;

        case 'store':
          this.filterParams.shopifyStore = [];
          this.popStoreFilter = false;
          ++this.cascaderKey;
          break;

        case 'date':
          this.filterParams.timeFilter = "";
          this.filterDate = [];
          this.filterParams.dateFrom = "";
          this.filterParams.dateTo = "";
          this.popDateFilter = false;
          break;

        case 'fulfill':
          this.filterParams.fulfillStatus = [];
          this.popFulfillFilter = false;
          break;

        case 'sort':
          this.filterParams.sortBy = "";
          this.popSortFilter = false;
          break;

        default:
          this.filterParams = JSON.parse(this.filterParamsDefault);
          this.codeName = '';
          this.filterDate = [];
      }

      this.resetPage();
    },
    toggleTreeNode: function toggleTreeNode(row) {
      if (row._HAS_CHILDREN) {
        this.setTreeExpand(row, !row._EXPAND);
      }
    },
    setTreeExpand: function setTreeExpand(row, isExpand) {
      var matchObj = XEUtils.findTree(this.tree6, function (item) {
        return item === row;
      });
      row._EXPAND = isExpand;

      if (matchObj) {
        XEUtils.eachTree(matchObj.item.children, function (item, index, items, path, parent) {
          item._VISIBLE = parent ? parent._EXPAND && parent._VISIBLE : isExpand;
        });
      }

      this.refreshTree();
    },
    refreshTree: function refreshTree() {
      var treeList = XEUtils.toTreeArray(this.tree6);
      this.fullList6 = treeList;
      this.tableData = treeList.filter(function (item) {
        return item._VISIBLE;
      });

      if (this.tableData.length) {
        var exandNum = 0,
            closeNum = 0;
        this.tableData.forEach(function (item) {
          if (item._LEVEL === 0) {
            if (item._EXPAND) {
              exandNum++;
            } else {
              closeNum++;
            }
          }
        });

        if (exandNum && !closeNum) {
          this.isAllExpand = true;
        }

        if (closeNum && !exandNum) {
          this.isAllExpand = false;
        }
      } // this.$emit("getTableHeight", this);

    },
    getItems: function getItems(type) {
      var _this11 = this;

      var arr = [];

      if (this.filterParams.shopifyStore[1]) {
        arr.push(this.filterParams.shopifyStore[1]);
      }

      if (!type) {
        this.loading = true;
        this.tableData = [];
        this.tableDataCopy = [];
      } else {
        if (this.bottomLoading) {
          return;
        }

        this.bottomLoading = true;
      }

      var params = {
        page: this.page,
        rowsPerPage: this.rowsPerPage,
        accountIds: arr,
        timeFilter: this.filterParams.timeFilter >= 6 ? "" : this.filterParams.timeFilter,
        dateFrom: this.filterParams.dateFrom,
        dateTo: this.filterParams.dateTo,
        fulfillmentStatus: this.filterParams.fulfillStatus,
        sort: this.filterParams.sortBy,
        keywords: this.filterParams.codeName,
        waitDispatch: 1
      };
      this.$apiCall("api.Invoice.shopifyOrders", params, function (r) {
        if (r.ErrorCode == "9999") {
          _this11.totalPage = r.Data.Pagination.totalPage;
          _this11.totalCount = r.Data.Pagination.totalCount;
          r.Data.Results.forEach(function (e, i) {
            _this11.$set(e, "shippingJson", JSON.parse(e.shippingJson));

            _this11.$set(e, "_id", e.id);

            _this11.$set(e, "_LEVEL", 0);

            _this11.$set(e, "_EXPAND", false);

            _this11.$set(e, "_VISIBLE", true);

            _this11.$set(e, "_HAS_CHILDREN", !!(e.items && e.items.length));

            _this11.$set(e, "_IS_LEAF", !e._HAS_CHILDREN);

            _this11.$set(e, "bg", i % 2);

            e.children = [];
            e.items.forEach(function (child, index) {
              child._LEVEL = 1;
              child._id = e.id + '_' + child.id + '_' + index + e.codeName;

              _this11.$set(child, "bg", i % 2);

              _this11.$set(child, "storeDefaultVendorId", e.storeDefaultVendorId);

              e.children.push(child);
            });

            _this11.tableData.push(e);
          });
          _this11.tree6 = _this11.tableData;
          _this11.tableDataCopy = _this11.tableData;

          _this11.$nextTick(function () {
            if (_this11.isAllExpand) _this11.allTreeExpand();
          });

          _this11.loading = false;

          if (type) {
            _this11.bottomLoading = false;
          }
        } else {
          _this11.loading = false;

          _this11.$elementMessage(r.Message, "error");
        }
      });
    },
    getStore: function getStore() {
      var _this12 = this;

      this.$apiCall("api.Invoice.findStores", {
        authStatus: 1
      }, function (r) {
        if (r.ErrorCode == 9999) {
          _this12.storeList = [];
          r.Data.Results.forEach(function (e) {
            _this12.storeList.push({
              shopifyAccountId: e.id,
              store: e.shopName
            });
          });

          _this12.options.map(function (item) {
            r.Data.Results.filter(function (a) {
              if (a.platform == item.id) {
                item.children.push({
                  label: a.shopName,
                  value: a.id
                });
              }
            });
          });
        } else {
          _this12.$message({
            message: r.Message,
            type: "error"
          });
        }
      });
    },
    onCopy: function onCopy(e) {
      this.$elementMessage('Has been copied to the clipboard', 'success');
    },
    onError: function onError(e) {
      this.$elementMessage('Copy to clipboard failed, please copy manually', 'error');
    }
  }
};